@font-face {
    font-family: "Nucleo";
    src: url("../../fonts/nucleo/nucleo.eot");
    src:
        url("../../fonts/nucleo/nucleo.eot") format("embedded-opentype"),
        url("../../fonts/nucleo/nucleo.woff2") format("woff2"),
        url("../../fonts/nucleo/nucleo.woff") format("woff"),
        url("../../fonts/nucleo/nucleo.ttf") format("truetype");
    font-weight: normal;
    font-style: normal;
}

/*------------------------
      base class definition
  -------------------------*/

.tim-icons {
    display: inline-block;
    font: normal normal normal 1em/1 "Nucleo";
    vertical-align: middle;
    speak: none;
    text-transform: none;
    /* Better Font Rendering */
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

.font-icon-detail {
    text-align: center;
    padding: 45px 0 30px;
    border: 1px solid #e44cc4;
    border-radius: 0.1875rem;
    margin: 15px 0;
    min-height: 168px;
}

.font-icon-detail i {
    color: #ffffff;
    font-size: 1.5em;
}

.font-icon-detail p {
    color: #e44cc4 !important;
    margin-top: 30px;
    padding: 0 10px;
    font-size: 0.7142em;
}

/*------------------------
    change icon size
  -------------------------*/

.tim-icons-sm {
    font-size: 0.8em;
}

.tim-icons-lg {
    font-size: 1.2em;
}

/* absolute units */

.tim-icons-16 {
    font-size: 16px;
}

.tim-icons-32 {
    font-size: 32px;
}

/*----------------------------------
    add a square/circle background
  -----------------------------------*/

.tim-icons-bg-square,
.tim-icons-bg-circle {
    padding: 0.35em;
}

.tim-icons-bg-circle {
    border-radius: 50%;
}

/*------------------------
    list icons
  -------------------------*/

/*------------------------
    spinning icons
  -------------------------*/

.tim-icons-is-spinning {
    -webkit-animation: tim-icons-spin 2s infinite linear;
    -moz-animation: tim-icons-spin 2s infinite linear;
    animation: tim-icons-spin 2s infinite linear;
}

@-webkit-keyframes tim-icons-spin {
    0% {
        -webkit-transform: rotate(0deg);
    }
    100% {
        -webkit-transform: rotate(360deg);
    }
}

@-moz-keyframes tim-icons-spin {
    0% {
        -moz-transform: rotate(0deg);
    }
    100% {
        -moz-transform: rotate(360deg);
    }
}

@keyframes tim-icons-spin {
    0% {
        -webkit-transform: rotate(0deg);
        -moz-transform: rotate(0deg);
        -ms-transform: rotate(0deg);
        -o-transform: rotate(0deg);
        transform: rotate(0deg);
    }
    100% {
        -webkit-transform: rotate(360deg);
        -moz-transform: rotate(360deg);
        -ms-transform: rotate(360deg);
        -o-transform: rotate(360deg);
        transform: rotate(360deg);
    }
}

